import { Component, OnInit } from '@angular/core';
import { SafeResourceUrl, DomSanitizer } from '@angular/platform-browser';
import { MatSnackBar } from '@angular/material';
import { NewUserSnackbarComponent } from '../new-user-snackbar/new-user-snackbar.component';
import { interval } from 'rxjs';

@Component({
  selector: 'app-first-page',
  templateUrl: './first-page.component.html',
  styleUrls: ['./first-page.component.scss']
})
export class FirstPageComponent implements OnInit {
  safeURL: SafeResourceUrl;
  videoURL = 'https://www.youtube.com/embed/PzkiNG-DS18';
  constructor(private sanitizer: DomSanitizer, private snackBar: MatSnackBar) {
    this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(this.videoURL);
  }

  openSnackBar() {
    this.snackBar.openFromComponent(NewUserSnackbarComponent, {
      duration: 3000,

    });
  }
  ngOnInit() {
    interval(5000).subscribe(x => this.openSnackBar());
   // this.openSnackBar();
  }
}
